<template>
  <div class="wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <Table :columns="columns" :data="tableData">
        <template slot-scope="{ row }" slot="2">
          <span style="color:#538FFF">{{row['2']}}</span>
        </template>
        <template slot-scope="{ row }" slot="reason">
          <button @click="lookReason(row)">原因</button>
        </template>
        <template slot-scope="{row}" slot="action">
          <div>
            <button @click="handleStop(row)">中止</button>
            <button @click="handleRecovery(row)">回复</button>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="currentPage"
        :total="total"
        :page-size="pageSize"
        :page-size-opts="[10,20,50]"
        show-sizer
        show-elevator
        show-total
      />
    </div>
    <!-- 案件中止 -->
    <Modal v-model="stopDialog" :footer-hide="true" title="案件中止">
      <Form ref="stopFormValidate" :model="stopForm" :rules="stopRule" :label-width="110">
        <FormItem label="起始日期" prop="startTime">
          <DatePicker  type="date" placeholder v-model="stopForm.startTime"></DatePicker>
        </FormItem>
        <!-- <FormItem label="结束日期">
          <DatePicker  type="date" placeholder v-model="stopForm.endTime"></DatePicker>
        </FormItem> -->
        <FormItem label="延期事由" prop="reason">
          <Select  v-model="stopForm.reason">
            <Option value="beijing">New York</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
        </FormItem>
        <FormItem label="材料附件" prop="file">
          <div style="display:flex">
            <Upload action="//jsonplaceholder.typicode.com/posts/" :before-upload="handleUpload">
              <Button type="primary" size="small">浏览...</Button>
            </Upload>
            <div style="margin-left:10px;height: 40px;">{{stopForm.file? stopForm.file.name: '未选择文件'}}</div>
          </div>
        </FormItem>
        <FormItem label="备注信息" prop="remarks">
          <Input
            v-model="stopForm.remarks"
            type="textarea"
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder="请输入备注信息"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button type="primary" @click="stopSubmit">提交中止申请</Button>
      </div>
    </Modal>
    <!-- 案件恢复 -->
    <Modal v-model="recoveryDialog" :footer-hide="true" title="案件恢复">
      <Form ref="recoverFormValidate" :model="recoverForm" :rules="recoverRule" :label-width="110">
        <FormItem label="结束日期" prop="endTime">
          <DatePicker :options="endTimeOptions" @on-change="changePicker" type="date" placeholder v-model="recoverForm.endTime"></DatePicker>
        </FormItem>
        <FormItem label="材料附件" prop="file">
          <div style="display:flex">
            <Upload action="//jsonplaceholder.typicode.com/posts/" :before-upload="handleUpload">
              <Button type="primary" size="small">浏览...</Button>
            </Upload>
            <div style="margin-left:10px;height: 40px;">{{recoverForm.file? recoverForm.file.name: '未选择文件'}}</div>
          </div>
        </FormItem>
        <FormItem label="备注信息" prop="remarks">
          <Input
            v-model="recoverForm.remarks"
            type="textarea"
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder="请输入备注信息"
          />
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button type="primary" @click="recoverSubmit">提交恢复申请</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
const columns = [
  { title: "案号", slot: "2" },
  { title: "申请人" },
  { title: "代理人" },
  { title: "被申请人" },
  { title: "中止时间" },
  { title: "恢复时间" },
  { title: "案件进度" },
  { title: "案件类型" },
  { title: "驳回原因", slot: "reason" },
  { title: "案件状态" },
  { title: "操作", align: "center", width: 300, slot: "action" }
];
export default {
  components: {
    listSearch
  },
  data() {
    return {
      searchData: {
        number: "",
        appeal: "",
        backAppeal: "",
        progress: [],
        caseType: []
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "appeal",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backAppeal",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "progress",
          label: "案件进度",
          placeholder: "请选择案件进度",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        },
        {
          type: "select",
          value: "caseType",
          label: "案件类型",
          placeholder: "请选择案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ]
        }
      ],
      currentPage: 1,
      total: 0,
      pageSize: 10,
      columns: columns,
      tableData: [{}],
      stopDialog: false, // 中止
      recoveryDialog:false, // 案件恢复
      stopForm: {
        startTime: "",
        reason: "",
        file: "",
        remarks: ""
      },
      stopRule: {
        startTime: [{required: true,type: "date",message: "请选择日期",trigger: "change"}],
        reason: [{ required: true, message: "请选择事由", trigger: "change" }],
        remarks: [{ required: true, message: "请输入备注信息", trigger: "change" }]
      },
      recoverForm:{
        endTime: "",
        file: "",
        remarks: ""
      },
      recoverRule:{
        endTime: [{required: true,type: "date",message: "请选择日期",trigger: "change"}],
        remarks: [{ required: true, message: "请输入备注信息", trigger: "change" }]
      },
      endTimeOptions:{}
      
    };
  },
  watch:{
    // 设置结束时间 不能小于开始时间
    'stopForm.startTime':function(val){
      this.endTimeOptions = {
        disabledDate: date => {
          return date && date < new Date(val).getTime()+24*60*60*1000
        }
      }
    }
  },
  methods: {
    goSearch() {},
    // 案件中止
    stopSubmit(){
        this.$refs["stopFormValidate"].validate(valid => {
        console.log(this.stopForm,valid);
        if (valid) {
            // 上传文件校验
            if(!this.stopForm.file){
               this.$Message.error("请上传文件"); 
               return
            }
          this.$Message.success("操作成功");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    // 案件恢复
    recoverSubmit(){
      this.$refs["recoverFormValidate"].validate(valid => {
              console.log(this.recoverForm,valid);
              if (valid) {
                if (new Date(this.stopForm.startTime) - new Date(this.recoverForm.endTime) >0) {
                  this.$Message.error('结束日期不能小于起始日期')
                  this.recoverForm.endTime = ''
                  return
                }
                  // 上传文件校验
                  if(!this.recoverForm.file){
                    this.$Message.error("请上传文件"); 
                    return
                  }
                this.$Message.success("操作成功");
                // this.isShowDate=false
              } else {
                this.$Message.error("Fail!");
              }
          });
    },
    // 查看原因
    lookReason(row){
      
    },
    // 案件中止
    handleStop(row) {
        this.stopDialog=true;
        this.$nextTick(()=>{
          this.$refs["stopFormValidate"].resetFields();
        })
    },
    // 案件恢复
    handleRecovery(row) {
      this.recoveryDialog=true;
    },
    handleUpload(file) {
      this.stopForm.file = file;
      return false;
    },
    changePicker(value,type){
      if (new Date(this.stopForm.startTime) - new Date(value) >0) {
        this.$Message.error('结束日期不能小于起始日期')
        this.recoverForm.endTime = ''
      }
      console.log(value,type);
      
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/.ivu-date-picker {
  width: 100%;
}
</style>
